.experianceCont {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
}

.experiance-inner {
  color: #e6ece2;
  background: #303516;
  padding: 20px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  margin: 10px 0;
}

.experiance-inner:nth-child(even) {
  color: #303516;
  background: #b0b4ae;
}
